import React from "react";
import ViewTable from "./ViewTable";
import ViewText from "./ViewText";

export default function ViewSet({ id, inputData }) {
  const { preText, tableColumnsLabel, tableData, postText } = inputData;
  return (
    <div id={id}>
      <ViewText data={preText}></ViewText>
      <ViewTable columns={tableColumnsLabel} data={tableData}></ViewTable>
      <ViewText data={postText}></ViewText>
    </div>
  );
}